/**
 * Navigation Component
 *
 * Handles the site navigation functionality including mobile menu,
 * dropdowns, and responsive behavior.
 */

import { getElement, getElements, delegateEvent } from '../utils/dom';

/**
 * Navigation module using revealing module pattern
 */
const Navigation = (function() {
  // Private variables
  let hamburger;
  let navLinks;
  let navGroups;
  let isInitialized = false;

  /**
   * Initialize the navigation
   * @param {Object} options - Configuration options
   */
  function init(options = {}) {
    if (isInitialized) return;

    try {
      // Get navigation elements
      hamburger = getElement('.hamburger');
      navLinks = getElement('.navbar .nav-links');
      navGroups = getElements('.nav-group');

      if (hamburger && navLinks) {
        // Set up hamburger menu toggle
        setupHamburgerMenu();

        // Set up dropdown menus
        setupDropdownMenus();

        // Set up click outside to close
        setupClickOutside();

        isInitialized = true;
        console.log('Navigation initialized successfully');
      } else {
        console.warn('Navigation elements not found in the DOM');
      }
    } catch (error) {
      console.error('Error initializing navigation:', error);
    }
  }

  /**
   * Set up hamburger menu functionality
   */
  function setupHamburgerMenu() {
    hamburger.addEventListener('click', function(e) {
      e.stopPropagation(); // Prevent click from bubbling to document
      toggleMenu();
    });

    // Close menu when clicking a regular link (not dropdown toggles)
    getElements('.navbar .nav-links a:not(.nav-group-title)').forEach(link => {
      link.addEventListener('click', () => {
        closeMenu();
      });
    });
  }

  /**
   * Set up dropdown menu functionality
   */
  function setupDropdownMenus() {
    navGroups.forEach(group => {
      const groupTitle = group.querySelector('.nav-group-title');

      if (groupTitle) {
        groupTitle.addEventListener('click', function(e) {
          e.preventDefault();
          e.stopPropagation();

          // Close other dropdowns
          navGroups.forEach(otherGroup => {
            if (otherGroup !== group) {
              otherGroup.classList.remove('active');
            }
          });

          // Toggle current dropdown
          group.classList.toggle('active');
        });
      }
    });
  }

  /**
   * Set up click outside to close menu
   */
  function setupClickOutside() {
    document.addEventListener('click', function(e) {
      // If the click is outside the navbar and the menu is active
      if (!e.target.closest('.navbar') && navLinks.classList.contains('active')) {
        closeMenu();
        closeAllDropdowns();
      }
    });
  }

  /**
   * Toggle the mobile menu
   */
  function toggleMenu() {
    hamburger.classList.toggle('active');
    navLinks.classList.toggle('active');
  }

  /**
   * Close the mobile menu
   */
  function closeMenu() {
    hamburger.classList.remove('active');
    navLinks.classList.remove('active');
  }

  /**
   * Close all dropdown menus
   */
  function closeAllDropdowns() {
    navGroups.forEach(group => {
      group.classList.remove('active');
    });
  }

  // Public API
  return {
    init,
    closeMenu,
    closeAllDropdowns
  };
})();

export default Navigation;
