/**
 * Image Optimizer Component
 *
 * Optimizes images with lazy loading and proper fetch priority.
 */

import { getElements } from '../utils/dom';

/**
 * ImageOptimizer module using revealing module pattern
 */
const ImageOptimizer = (function() {
  // Private variables
  let isInitialized = false;

  // Default configuration
  const defaults = {
    imageSelector: 'img:not([loading])',
    aboveTheFoldPriority: 'high',
    belowTheFoldPriority: 'low',
    addDecoding: true
  };

  // Current options
  let options = {};

  /**
   * Initialize image optimization
   * @param {Object} userOptions - Configuration options
   */
  function init(userOptions = {}) {
    if (isInitialized) return;

    try {
      // Merge default options with user options
      options = { ...defaults, ...userOptions };

      // Get all images on the page that don't have loading attribute
      const images = getElements(options.imageSelector);

      if (images.length > 0) {
        // Apply lazy loading to images
        applyLazyLoading(images);

        // Add decoding="async" to images if configured
        if (options.addDecoding) {
          addAsyncDecoding(images);
        }

        isInitialized = true;
        console.log('Image Optimizer component initialized successfully');
      } else {
        console.warn('No images found to optimize');
      }
    } catch (error) {
      console.error('Error initializing image optimizer:', error);
    }
  }

  /**
   * Check if an element is above the fold (in viewport on page load)
   * @param {Element} element - Element to check
   * @returns {boolean} - True if element is above the fold
   */
  function isAboveTheFold(element) {
    const rect = element.getBoundingClientRect();
    return rect.top < window.innerHeight;
  }

  /**
   * Apply lazy loading to images
   * @param {NodeList} images - Collection of image elements
   */
  function applyLazyLoading(images) {
    images.forEach(function(img) {
      // Skip images that already have loading attribute
      if (img.hasAttribute('loading')) {
        return;
      }

      // If image is above the fold, use eager loading, otherwise use lazy loading
      if (isAboveTheFold(img)) {
        img.setAttribute('loading', 'eager');
        img.setAttribute('fetchpriority', options.aboveTheFoldPriority);
      } else {
        img.setAttribute('loading', 'lazy');
        img.setAttribute('fetchpriority', options.belowTheFoldPriority);
      }
    });
  }

  /**
   * Add decoding="async" to images
   * @param {NodeList} images - Collection of image elements
   */
  function addAsyncDecoding(images) {
    images.forEach(function(img) {
      if (!img.hasAttribute('decoding')) {
        img.setAttribute('decoding', 'async');
      }
    });
  }

  /**
   * Optimize a specific image element
   * @param {Element} img - Image element to optimize
   */
  function optimizeImage(img) {
    if (!img.hasAttribute('loading')) {
      if (isAboveTheFold(img)) {
        img.setAttribute('loading', 'eager');
        img.setAttribute('fetchpriority', options.aboveTheFoldPriority);
      } else {
        img.setAttribute('loading', 'lazy');
        img.setAttribute('fetchpriority', options.belowTheFoldPriority);
      }
    }

    if (options.addDecoding && !img.hasAttribute('decoding')) {
      img.setAttribute('decoding', 'async');
    }
  }

  // Public API
  return {
    init,
    optimizeImage
  };
})();

export default ImageOptimizer;
