/**
 * DOM Utility Functions
 * 
 * A collection of helper functions for DOM manipulation.
 */

/**
 * Get an element by selector
 * @param {string} selector - CSS selector
 * @returns {Element|null} - DOM element or null if not found
 */
export const getElement = (selector) => document.querySelector(selector);

/**
 * Get multiple elements by selector
 * @param {string} selector - CSS selector
 * @returns {NodeList} - Collection of DOM elements
 */
export const getElements = (selector) => document.querySelectorAll(selector);

/**
 * Check if an element is visible in viewport
 * @param {Element} element - DOM element to check
 * @returns {boolean} - True if element is in viewport
 */
export const isInViewport = (element) => {
  const rect = element.getBoundingClientRect();
  return (
    rect.top >= 0 &&
    rect.left >= 0 &&
    rect.bottom <= (window.innerHeight || document.documentElement.clientHeight) &&
    rect.right <= (window.innerWidth || document.documentElement.clientWidth)
  );
};

/**
 * Add event listener with delegation
 * @param {string} eventType - Type of event (click, mouseover, etc.)
 * @param {string} selector - CSS selector for target elements
 * @param {Function} callback - Event handler function
 */
export const delegateEvent = (eventType, selector, callback) => {
  document.addEventListener(eventType, (event) => {
    const target = event.target.closest(selector);
    if (target) {
      callback(event, target);
    }
  });
};

export default {
  getElement,
  getElements,
  isInViewport,
  delegateEvent
};
