/**
 * Tabs Component
 *
 * Handles tab functionality for content sections.
 */

import { getElements, delegateEvent } from '../utils/dom';

/**
 * Tabs module using revealing module pattern
 */
const Tabs = (function() {
  // Private variables
  let tabButtons;
  let tabPanels;
  let isInitialized = false;

  // Default configuration
  const defaults = {
    tabsContainerSelector: '.tabs-container',
    tabButtonSelector: '.tab-button',
    tabPanelSelector: '.tab-panel',
    activeClass: 'active'
  };

  // Current options
  let options = {};

  /**
   * Initialize tabs
   * @param {Object} userOptions - Configuration options
   */
  function init(userOptions = {}) {
    if (isInitialized) return;

    try {
      // Merge default options with user options
      options = { ...defaults, ...userOptions };

      // Get tab containers
      const tabContainers = getElements(options.tabsContainerSelector);

      if (tabContainers.length > 0) {
        // Set up tabs functionality for each container
        tabContainers.forEach(container => {
          setupTabs(container);
        });

        isInitialized = true;
        console.log('Tabs component initialized successfully');
      } else {
        console.warn('No tab containers found with selector:', options.tabsContainerSelector);
      }
    } catch (error) {
      console.error('Error initializing tabs:', error);
    }
  }

  /**
   * Set up tabs functionality for a container
   * @param {Element} container - The tabs container element
   */
  function setupTabs(container) {
    const buttons = container.querySelectorAll(options.tabButtonSelector);
    const panels = container.querySelectorAll(options.tabPanelSelector);

    if (buttons.length > 0 && panels.length > 0) {
      // Add click event listeners to tab buttons
      buttons.forEach(button => {
        button.addEventListener('click', () => {
          // Get the target tab panel
          const targetId = button.getAttribute('data-tab');
          const targetPanel = document.getElementById(`${targetId}-panel`);

          // Deactivate all tabs
          buttons.forEach(btn => btn.classList.remove(options.activeClass));
          panels.forEach(panel => panel.classList.remove(options.activeClass));

          // Activate the clicked tab
          button.classList.add(options.activeClass);
          if (targetPanel) {
            targetPanel.classList.add(options.activeClass);
          }
        });
      });

      // Activate the first tab by default if none are active
      const activeButton = container.querySelector(`${options.tabButtonSelector}.${options.activeClass}`);
      if (!activeButton && buttons.length > 0) {
        buttons[0].click();
      }
    }
  }

  /**
   * Activate a specific tab by ID
   * @param {string} tabId - ID of the tab to activate
   */
  function activateTab(tabId) {
    const button = document.querySelector(`${options.tabButtonSelector}[data-tab="${tabId}"]`);
    if (button) {
      button.click();
    }
  }

  // Public API
  return {
    init,
    activateTab
  };
})();

export default Tabs;
