/**
 * Progress Bar Component
 *
 * Handles the horizontal progress bar for the 9-step journey.
 */

import { getElement } from '../utils/dom';
import { saveToLocalStorage, getFromLocalStorage } from '../utils/storage';

/**
 * ProgressBar module using revealing module pattern
 */
const ProgressBar = (function() {
  // Private variables
  const STEP_PAGES = [
    { url: '/pages/self-assessment.html', step: 1 },
    { url: '/pages/industry-research.html', step: 2 },
    { url: '/pages/skill-translation-hub.html', step: 3 },
    { url: '/pages/resume-personal-branding.html', step: 4 },
    { url: '/pages/networking-strategies.html', step: 5 },
    { url: '/pages/job-search-resources.html', step: 6 },
    { url: '/pages/interview-preparation.html', step: 7 },
    { url: '/pages/compensation-benefits.html', step: 8 },
    { url: '/pages/transition-checklist.html', step: 9 }
  ];

  // Step names for tooltips
  const STEP_NAMES = [
    'Self-Assessment',
    'Industry Research',
    'Skill Translation',
    'Resume & Branding',
    'Networking',
    'Job Search',
    'Interview Prep',
    'Compensation',
    'Transition Checklist'
  ];

  let progressFill;
  let currentStepElement;
  let progressBar;
  let progressWrapper;
  let isInitialized = false;

  // Storage key for progress
  const STORAGE_KEY = 'currentStep';

  /**
   * Initialize progress bar
   */
  function init() {
    if (isInitialized) return;

    try {
      // Get progress bar elements
      progressFill = getElement('#nav-progress-fill');
      currentStepElement = getElement('#current-step');
      progressBar = getElement('#nav-progress-bar');
      progressWrapper = getElement('.nav-progress-wrapper');

      if (!progressFill || !currentStepElement || !progressBar) {
        console.warn('Progress bar elements not found in the DOM');
        return;
      }

      // Update progress based on current page
      updateProgress();

      // Make progress bar clickable to go to roadmap
      setupClickHandler();

      isInitialized = true;
      console.log('Progress Bar component initialized successfully');
    } catch (error) {
      console.error('Error initializing progress bar:', error);
    }
  }

  /**
   * Set up click handler for progress bar
   */
  function setupClickHandler() {
    if (progressBar) {
      progressBar.style.cursor = 'pointer';
      progressBar.title = 'View Transition Roadmap';
      progressBar.addEventListener('click', function() {
        window.location.href = '/pages/transition-roadmap.html';
      });
    }
  }

  /**
   * Update progress bar based on current page
   */
  function updateProgress() {
    // Get current step
    const displayStep = getCurrentStep();

    // Update progress bar
    if (displayStep > 0) {
      // Calculate percentage (steps are 1-9, so we need to convert to 0-100%)
      const progressPercentage = ((displayStep - 1) / (STEP_PAGES.length - 1)) * 100;

      // Update fill width
      progressFill.style.width = `${progressPercentage}%`;

      // Update step text
      currentStepElement.textContent = displayStep;

      // Show the progress bar wrapper
      if (progressWrapper) {
        progressWrapper.style.display = 'flex';
      }
    } else {
      // Hide progress bar if no step is active
      if (progressWrapper) {
        progressWrapper.style.display = 'none';
      }
    }
  }

  /**
   * Get current step from URL or localStorage
   * @returns {number} - Current step (1-9)
   */
  function getCurrentStep() {
    // Get current page URL and determine current step
    const currentPath = window.location.pathname;
    const currentPageData = STEP_PAGES.find(page => page.url === currentPath);
    const currentStep = currentPageData ? currentPageData.step : 0;

    // If we're on a step page, save progress
    if (currentStep > 0) {
      saveToLocalStorage(STORAGE_KEY, currentStep);
    }

    // Get the step to display (either current page or last saved step)
    return currentStep || getFromLocalStorage(STORAGE_KEY, 0);
  }

  /**
   * Set current step manually
   * @param {number} step - Step number (1-9)
   */
  function setCurrentStep(step) {
    if (step >= 0 && step <= STEP_PAGES.length) {
      saveToLocalStorage(STORAGE_KEY, step);
      updateProgress();
    }
  }

  /**
   * Get step name by number
   * @param {number} step - Step number (1-9)
   * @returns {string} - Step name
   */
  function getStepName(step) {
    if (step >= 1 && step <= STEP_NAMES.length) {
      return STEP_NAMES[step - 1];
    }
    return '';
  }

  // Public API
  return {
    init,
    updateProgress,
    getCurrentStep,
    setCurrentStep,
    getStepName
  };
})();

export default ProgressBar;
