/**
 * Accordion Component
 *
 * Handles accordion functionality for FAQ sections and other collapsible content.
 */

import { getElements, delegateEvent } from '../utils/dom';

/**
 * Accordion module using revealing module pattern
 */
const Accordion = (function() {
  // Private variables
  let accordionItems;
  let activeClass = 'active';
  let isInitialized = false;

  // Default configuration
  const defaults = {
    triggerSelector: '.faq-question',
    contentSelector: '.faq-answer',
    closeOthers: true,
    animateHeight: true
  };

  // Current options
  let options = {};

  /**
   * Initialize accordions
   * @param {Object} userOptions - Configuration options
   */
  function init(userOptions = {}) {
    if (isInitialized) return;

    try {
      // Merge default options with user options
      options = { ...defaults, ...userOptions };

      // Get accordion items
      accordionItems = getElements(options.triggerSelector);

      if (accordionItems.length > 0) {
        // Set up accordion functionality
        setupAccordions();

        isInitialized = true;
        console.log('Accordion component initialized successfully');
      } else {
        console.warn('No accordion items found with selector:', options.triggerSelector);
      }
    } catch (error) {
      console.error('Error initializing accordion:', error);
    }
  }

  /**
   * Set up accordion functionality
   */
  function setupAccordions() {
    accordionItems.forEach(item => {
      item.addEventListener('click', () => {
        const content = item.nextElementSibling;

        // Check if content matches our content selector
        if (content && content.matches(options.contentSelector)) {
          toggleItem(item, content);
        }
      });
    });
  }

  /**
   * Toggle an accordion item
   * @param {Element} trigger - The accordion trigger element
   * @param {Element} content - The accordion content element
   */
  function toggleItem(trigger, content) {
    const isOpen = content.style.maxHeight;

    // Close other items if configured to do so
    if (options.closeOthers) {
      closeAllItems();
    }

    // Toggle the clicked item
    if (!isOpen) {
      // Open this item
      if (options.animateHeight) {
        content.style.maxHeight = content.scrollHeight + 'px';
      } else {
        content.style.display = 'block';
      }
      trigger.classList.add(activeClass);
    } else {
      // Close this item
      if (options.animateHeight) {
        content.style.maxHeight = null;
      } else {
        content.style.display = 'none';
      }
      trigger.classList.remove(activeClass);
    }
  }

  /**
   * Close all accordion items
   */
  function closeAllItems() {
    const allContents = getElements(options.contentSelector);
    const allTriggers = getElements(options.triggerSelector);

    allContents.forEach(content => {
      if (options.animateHeight) {
        content.style.maxHeight = null;
      } else {
        content.style.display = 'none';
      }
    });

    allTriggers.forEach(trigger => {
      trigger.classList.remove(activeClass);
    });
  }

  /**
   * Open a specific accordion item by index
   * @param {number} index - Index of the item to open
   */
  function openItem(index) {
    if (index >= 0 && index < accordionItems.length) {
      const trigger = accordionItems[index];
      const content = trigger.nextElementSibling;

      if (content && content.matches(options.contentSelector)) {
        // Close others first
        if (options.closeOthers) {
          closeAllItems();
        }

        // Open the specified item
        if (options.animateHeight) {
          content.style.maxHeight = content.scrollHeight + 'px';
        } else {
          content.style.display = 'block';
        }
        trigger.classList.add(activeClass);
      }
    }
  }

  // Public API
  return {
    init,
    openItem,
    closeAllItems
  };
})();

export default Accordion;
