/**
 * Testimonials Component
 *
 * Handles testimonial slider functionality.
 */

import { getElements } from '../utils/dom';

/**
 * Testimonials module using revealing module pattern
 */
const Testimonials = (function() {
  // Private variables
  let testimonials;
  let dots;
  let currentIndex = 0;
  let autoRotateInterval;
  let isInitialized = false;

  // Default configuration
  const defaults = {
    testimonialSelector: '.testimonial',
    dotSelector: '.testimonial-dot',
    activeClass: 'active',
    autoRotate: true,
    autoRotateInterval: 5000,
    fadeEffect: false
  };

  // Current options
  let options = {};

  /**
   * Initialize testimonial slider
   * @param {Object} userOptions - Configuration options
   */
  function init(userOptions = {}) {
    if (isInitialized) return;

    try {
      // Merge default options with user options
      options = { ...defaults, ...userOptions };

      // Get testimonials and dots
      testimonials = getElements(options.testimonialSelector);
      dots = getElements(options.dotSelector);

      if (testimonials.length > 0 && dots.length > 0) {
        // Set up testimonial slider
        setupTestimonials();

        // Start auto-rotation if enabled
        if (options.autoRotate) {
          startAutoRotate(options.autoRotateInterval);
        }

        isInitialized = true;
        console.log('Testimonials component initialized successfully');
      } else {
        console.warn('Testimonials or dots not found in the DOM');
      }
    } catch (error) {
      console.error('Error initializing testimonials:', error);
    }
  }

  /**
   * Set up testimonial slider functionality
   */
  function setupTestimonials() {
    // Hide all testimonials except the first one
    testimonials.forEach((testimonial, index) => {
      if (index !== 0) {
        testimonial.style.display = 'none';
      }
    });

    // Add click event to dots
    dots.forEach((dot, index) => {
      dot.addEventListener('click', () => {
        showTestimonial(index);
      });
    });
  }

  /**
   * Show a specific testimonial
   * @param {number} index - Index of testimonial to show
   */
  function showTestimonial(index) {
    if (index < 0 || index >= testimonials.length) {
      console.warn('Invalid testimonial index:', index);
      return;
    }

    // Update current index
    currentIndex = index;

    // Hide all testimonials
    testimonials.forEach(testimonial => {
      testimonial.style.display = 'none';
    });

    // Show the selected testimonial
    testimonials[index].style.display = 'block';

    // Update active dot
    dots.forEach(dot => dot.classList.remove(options.activeClass));
    dots[index].classList.add(options.activeClass);
  }

  /**
   * Show the next testimonial
   */
  function showNextTestimonial() {
    const nextIndex = (currentIndex + 1) % testimonials.length;
    showTestimonial(nextIndex);
  }

  /**
   * Show the previous testimonial
   */
  function showPreviousTestimonial() {
    const prevIndex = (currentIndex - 1 + testimonials.length) % testimonials.length;
    showTestimonial(prevIndex);
  }

  /**
   * Start auto-rotation of testimonials
   * @param {number} interval - Rotation interval in milliseconds
   */
  function startAutoRotate(interval = 5000) {
    // Clear any existing interval
    stopAutoRotate();

    // Set new interval
    autoRotateInterval = setInterval(() => {
      showNextTestimonial();
    }, interval);
  }

  /**
   * Stop auto-rotation of testimonials
   */
  function stopAutoRotate() {
    if (autoRotateInterval) {
      clearInterval(autoRotateInterval);
      autoRotateInterval = null;
    }
  }

  // Public API
  return {
    init,
    showTestimonial,
    showNextTestimonial,
    showPreviousTestimonial,
    startAutoRotate,
    stopAutoRotate
  };
})();

export default Testimonials;
