/**
 * WebP Detection Utility
 *
 * Detects browser support for WebP image format and adds appropriate class to HTML element.
 */

/**
 * Check for WebP support
 * @param {Function} callback - Callback function to run with result (true if supported, false if not)
 */
export function checkWebpSupport(callback) {
  try {
    const img = new Image();

    img.onload = function() {
      const result = (img.width > 0) && (img.height > 0);
      callback(result);
    };

    img.onerror = function() {
      callback(false);
    };

    // Base64 representation of a small WebP image
    img.src = 'data:image/webp;base64,UklGRhoAAABXRUJQVlA4TA0AAAAvAAAAEAcQERGIiP4HAA==';
  } catch (error) {
    console.error('Error checking WebP support:', error);
    callback(false);
  }
}

/**
 * Initialize WebP detection
 * Adds 'webp' or 'no-webp' class to the HTML element
 */
export function initWebpDetection() {
  checkWebpSupport(function(support) {
    try {
      if (support) {
        document.documentElement.classList.add('webp');
      } else {
        document.documentElement.classList.add('no-webp');
      }
      console.log('WebP support detected:', support ? 'Supported' : 'Not supported');
    } catch (error) {
      console.error('Error initializing WebP detection:', error);
    }
  });
}

/**
 * Check if WebP is supported synchronously
 * Note: This will only work after initWebpDetection has run
 * @returns {boolean} - True if WebP is supported, false if not, null if unknown
 */
export function isWebpSupported() {
  if (document.documentElement.classList.contains('webp')) {
    return true;
  } else if (document.documentElement.classList.contains('no-webp')) {
    return false;
  }
  return null; // Unknown state
}

export default {
  checkWebpSupport,
  initWebpDetection,
  isWebpSupported
};
